.simple-chatbots-icon {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: var(--br-11xl);
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.frame-child {
  position: absolute;
  top: 40px;
  left: 38.5px;
  border-radius: 50%;
  background-color: var(--color-limegreen);
  width: 8px;
  height: 8px;
  z-index: 1;
}
.simple-chatbots-parent {
  height: 50px;
  width: 50px;
  position: relative;
}
.timpu {
  margin: 0;
  position: relative;
  font-size: inherit;
  font-weight: 600;
  font-family: inherit;
  display: inline-block;
  min-width: 59px;
  white-space: nowrap;
}
.timpu-wrapper {
  height: 25px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: 0;
  box-sizing: border-box;
}
.vector-icon {
  height: 15px;
  width: 14.4px;
  position: relative;
}
.frame-parent2 {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: 0 var(--padding-29xl) 0 0;
  gap: var(--gap-3xs);
}
.chat-assistant {
  height: 25px;
  flex: 1;
  position: relative;
  font-weight: 500;
  display: flex;
  align-items: center;
  white-space: nowrap;
}
.chat-assistant-wrapper {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: var(--font-size-mini);
}
.frame-parent1 {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding: var(--padding-8xs) 0;
}
.frame-div {
  width: 191px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-3xs);
}
.frame-item {
  height: 8px;
  width: 8px;
  position: relative;
  border-radius: 50%;
  background-color: var(--color-limegreen);
}
.online {
  flex: 1;
  position: relative;
  line-height: 10px;
  font-weight: 500;
}
.ellipse-parent {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: flex-start;
  gap: var(--gap-8xs);
}
.krunk-container,
.krunk-container-inner {
  width: 62px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 0 var(--padding-mini);
  box-sizing: border-box;
  font-size: var(--font-size-mini);
}
.krunk-container {
  width: 360px;
  border-radius: var(--br-xl) var(--br-xl) 0 0;
  background-color: var(--color-aliceblue-200);
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.25);
  overflow: hidden;
  flex-direction: row;
  align-items: flex-end;
  justify-content: space-between;
  padding: var(--padding-3xs) var(--padding-xl);
  top: 0;
  z-index: 99;
  position: sticky;
  max-width: 100%;
  gap: var(--gap-xl);
  text-align: left;
  font-size: var(--font-size-lg);
  color: var(--color-dodgerblue);
  font-family: var(--font-poppins);
}
